.navbar {
  padding: 20px 50px;
  position: absolute;
  z-index: 1000;
  width: 100%;
}

.navbar--logo-holder {
  width: 100%;
  display: flex;
  flex-direction: row;
}

.nav-row {
  width: 100%;
  display: flex;
  align-items: center;
}

.navbar-sticky {
  background: rgba(255, 255, 255, 0.95);
  position: fixed;
  top: 0;
  left: 0;
  animation: moveDown 0.5s ease-in-out;
}

.navbar-button {
  color: white;
  text-decoration: none;
  background-color: #00b04f;
  border-color: #00b04f;
  font-size: 1em;
  padding: 10px;
  margin-left: auto;
  width: 260px;
}

.navbar-button:hover,
.navbar-button:focus {
  background-color: #ffffff;
  border-color: #00b04f;
  color: #00b04f;
}

.navbar--logo {
  width: 15%;
}

@keyframes moveDown {
  from {
    transform: translateY(-5rem);
  }
  to {
    transform: translateY(0rem);
  }
}

@media only screen and (max-width: 400px) {
  .navbar {
    padding: 15px;
  }
}

